<template>
  <div class="count-box">
    <button @click="handsub()" class="minus">-</button>
    <input class="inp" @change="handChange" :value="value" type="text">
    <button @click="handadd()" class="add">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    handadd () {
      this.$emit('input', this.value + 1)
    },
    handsub () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    handChange (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }
      this.$emit('input', num)
    }

  }
}
</script>

<style lang="less">
.count-box{
    width: 110px;
    display: flex;
    .add, .minus{
        width: 30px;
        height: 30px;
        outline: none;
        border: none;
        background-color: #efefef;
    }
    .inp {
        width: 40px;
        height: 30px;
        margin: 0 5px;
        border: none;
        background-color: #efefef;
        text-align: center;
    }
}
</style>
